<template>


<div class="container my-3">

    <!-- Modal Ver Detalles -->
    <b-modal :id="infoModal.id"
            :title="infoModal.title"
            ok-only
            @hide="resetInfoModal"
            :codigo="infoModal.codigo"
            hide-footer>
      <tr v-for="(value, key) in infoModal.content" :key="key">
        <th>{{ key }}</th>
        <td>{{ value }}</td>
      </tr>
    </b-modal>

    <!-- Modal Pago -->
    <b-modal v-model="modalPago"
            centered
            size="md"
            :title="textoModalPagos"
            :header-bg-variant="headerBgVariant"
            :header-text-variant="headerTextVariant">
        <b-container fluid class="container">
            <div class="col-12">
				<strong><label>Importe Nota de Remisión:</label></strong>
                <input v-model="notaRemisionSeleccionada.importeFormato"
                        disabled
                        class="form-control"
                        />
            </div>
            <div class="col-12">
				<strong><label>Total Abonado:</label></strong>
                <input v-model="notaRemisionSeleccionada.abonadoFormato"
                        disabled
                        class="form-control"
                        />
            </div>
            <div class="col-12">
				<strong><label>Importe a Abonar:</label></strong>
                <input v-model="importeAbonar"
                        class="form-control"
                        type="number"/>
            </div>

        </b-container>

        <template #modal-footer>
          <div class="w-100">

			<b-button @click="modalPago = false"
                        :disabled="disabledButtons"
						variant="danger"
						class="float-right">
              Cancelar
            </b-button>
            <b-button @click="GuardarPago()"
                        :disabled="disabledButtons"
						variant="primary"
						class="float-right mr-2">
              Aceptar
            </b-button>
          </div>
        </template>
    </b-modal>

    <div class="mt-3">
        <h3><i class="fas fa-clipboard-list"></i> Notas de Remisión</h3>
    </div>
    <b-row>
        <div class="form-group col-sm-12 col-md-6 col-lg-5 col-xl-4 mb-1">
            <label>Desde:</label>
            <b-form-datepicker v-model="desde" class="mb-2" locale="es" size="sm"></b-form-datepicker>
        </div>
        <div class="form-group col-sm-12 col-md-6 col-lg-5 col-xl-4 mb-1">
            <label>Hasta:</label>
            <b-form-datepicker v-model="hasta" class="mb-2" locale="es" size="sm"></b-form-datepicker>
        </div>
    </b-row>
    <b-row class="d-flex justify-content-between">
        <div class="col-sm-12 col-md-4 col-lg-2 mb-2">
            <b-button @click="buscaNotasRemisionCobranza" block variant="success" class="mr-2">
                <i class="fa fa-search"></i> Buscar
            </b-button>
		</div>
    </b-row>

    <!-- User Interface controls -->
    <b-row class="my-2">
        <b-col md="4">
            <b-form-group label="Filtrar"
                        label-cols-sm="1"
                        label-cols-md="2"
                        label-size="sm"
                        label-for="filterInput">
                <b-input-group size="sm">
                    <b-form-input v-model="filter"
                                type="search"
                                id="filterInput"
                                placeholder="Escribe el filtro..."
                                autocomplete="off">
                    </b-form-input>
                    <b-input-group-append>
                        <b-button :disabled="!filter" @click="filter = ''">
                            <i class="fas fa-times"></i>
                        </b-button>
                    </b-input-group-append>
                </b-input-group>
            </b-form-group>
        </b-col>
        <b-col md="3">
            <b-form-group label="Por página"
                        label-cols-sm="2"
                        label-cols-md="2"
                        label-cols-lg="4"
                        label-size="sm"
                        label-for="perPageSelect">
                <b-form-select v-model="perPage"
                            id="perPageSelect"
                            size="sm"
                            :options="pageOptions">
                </b-form-select>
            </b-form-group>
        </b-col>
    </b-row>

    <!-- Main table element -->
    <b-table small
            stacked="md"
            head-variant="dark"
            :hover="hover"
            :striped="striped"
            :bordered="bordered"
            :items="items"
            :fields="fields"
            :current-page="currentPage"
            :per-page="perPage"
            :filter="filter"
            :filterIncludedFields="filterOn"
            :sort-by.sync="sortBy"
            :sort-desc.sync="sortDesc"
            :sort-direction="sortDirection"
            @filtered="onFiltered">

        <template v-slot:cell(estatus)="row">
            <b-badge variant="secondary" v-if="row.item.estatus === 'P'" class="p-2">
                Pendiente
            </b-badge>
            <b-badge variant="danger" v-if="row.item.estatus === 'C'" class="p-2">
                Cancelada
            </b-badge>
            <b-badge variant="primary" v-if="row.item.estatus === 'S'" class="p-2">
                Surtiendo
            </b-badge>
            <b-badge variant="success" v-if="row.item.estatus === 'T'" class="p-2">
                Terminada
            </b-badge>
        </template>

        <template v-slot:cell(estatusPago)="row">
            <b-badge variant="secondary" v-if="row.item.estatusPago === 'P'" class="p-2">
                Pendiente
            </b-badge>
            <b-badge variant="primary" v-if="row.item.estatusPago === 'A'" class="p-2">
                Abonada
            </b-badge>
            <b-badge variant="success" v-if="row.item.estatusPago === 'T'" class="p-2">
                Pagada
            </b-badge>
        </template>

        <template v-slot:cell(actions)="row">
            <!-- @click="modal(row.item, row.index, $event.target)" -->
            <b-button 
                    :to="rutaDetalle + row.item.folio"
                    size="sm"
                    class="mr-1"
                    v-b-tooltip.hover
                    title="Ver Detalle"
                    variant="outline-info">
                <i class="fa fa-eye"></i>
            </b-button>

            <b-button @click="PagarNotaRemision(row.item)"
                    size="sm"
                    class="mr-1"
                    v-b-tooltip.hover
                    title="Pagar Nota Remision"
                    variant="dark">
                <i class="fa fa-money-bill text-success"></i>
            </b-button>

        </template>

    </b-table>
        
    <b-pagination v-model="currentPage"
                    class="mt-1"
                    :total-rows="totalRows"
                    :per-page="perPage"
                    align="center"
                    aria-controls="my-table">
    </b-pagination>
    
</div>

</template>

<script>

import servicio from '@/services/servicio-cobranza';
import Swal from 'sweetalert2'

export default {
    data(){
        const now = new Date()
        const yesterday = new Date(now.getFullYear(), now.getMonth(), now.getDate()-1)
        const today = new Date(now.getFullYear(), now.getMonth(), now.getDate())
        var yesterdayDate = yesterday.toISOString().slice(0,10);
        var todayDate = today.toISOString().slice(0,10);

        return{
            desde: yesterdayDate,
            hasta: todayDate,
            items: [],

            totalRows: 1,
            currentPage: 1,
            perPage: 5,
            pageOptions: [5, 10, 15],
            sortBy: "",
            sortDesc: false,
            bordered: true,
            hover: true,
            striped: true,
            sortDirection: "asc",
            filter: null,
            filterOn: [],

            fields: [
                { key: "folio", label: "Folio", sortable: true, class: "text-center" },
                { key: "fechaFormato", label: "Fecha", sortable: true, class: "text-center" },
                { key: "vendedor", label: "Vendedor", sortable: true, class: "text-center" },
                { key: "cliente", label: "Cliente", sortable: true, class: "text-center" },
                // { key: "obra", label: "Obra", sortable: true, class: "text-center" },
                { key: "estatus", label: "Estatus", sortable: true, class: "text-center" },
                { key: "estatusPago", label: "Estatus del Pago", sortable: true, class: "text-center" },
                { key: "actions", label: "", class: "text-center" },
            ],

            infoModal: {
                id: "info-modal",
                title: "",
                content: "",
                codigo: "",
            },
            idSeleccionado: 0,
            pagoTotalNotaRemision: false,
            procederPago: 0,
            modalPago: false,
            headerBgVariant: "dark",
            headerTextVariant: "light",
            textoModalPagos: "",

            notaRemisionSeleccionada: {},
            importeAbonar: 0.0,
            disabledButtons: false,
            rutaDetalle: "/cobranza/notas-de-remision/detalle/",


        }
    },
    components: {
        // TableSortable,
    },
    async mounted() {
        await this.ObtenerNotasRemision();
        await this.CargarRenglones();
    },
    created() {
    },
    computed: {
        sortOptions() {
            // Create an options list from our fields
            return this.fields
                        .filter((f) => f.sortable)
                        .map((f) => {
                            return { text: f.label, value: f.key };
                        });
        },
    },
    methods: {

        onFiltered(filteredItems) {
            // Trigger pagination to update the number of buttons/pages due to filtering
            this.totalRows = filteredItems.length;
            this.currentPage = 1;
        },

        async buscaNotasRemisionCobranza() {
            await this.ObtenerNotasRemision();
        },

        async ObtenerNotasRemision() {
            this.$loading(true);
            await servicio.obtenerNotasRemisionCobranza(this.desde, this.hasta)
                            .then((r) => {

                                if(r.value) {
                                    this.items = r.data;
                                    this.$loading(false);
                                } else {
                                    this.$loading(false);
                                    // Swal.fire({
                                    //     title: "¡ALERTA!",
                                    //     text: r.message,
                                    //     icon: "error",
                                    // });
                                }
                                
                            })
                            .catch((error) => {
                                console.log(error);
                            });
        },

        CargarRenglones() {
            setTimeout(() => {
                this.totalRows = this.items.length;
            }, 1000);
        },

        modal(item, index, button) {
            let content = "";
            this.idSeleccionado = item.codigo;
            //Checo si la ultimaParteLink viene en el metodo
            this.infoModal.title = `Datos de la Nota de Remisión #${item.folio}`;
                content += `{ `;
                content += `"Folio" : " : ${item.folio}\\n\\n", `;
                content += `"Cliente" : " : ${item.cliente}\\n\\n",`;
                content += `"Fecha" : " : ${item.fechaFormato}\\n\\n",`;
                // content += `"Hora de Salida" : " : ${item.horaSalida}\\n\\n",`;
                content += `"Obra" : " : ${item.obra}\\n\\n",`;
                content += `"Vendedor" : " : ${item.vendedor}\\n\\n",`;
                content += `"Equipo" : " : ${item.equipo}\\n\\n",`;
                content += `"Operador" : " : ${item.operador}\\n\\n",`;
                content += `"nomenclatura" : " : ${item.producto}\\n\\n",`;
                content += `"producto" : " : ${item.productoDescripcion}\\n\\n",`;
                content += `"Cantidad" : " : ${item.cantidad} m³\\n\\n",`;
                content += `"Usuario" : " : ${item.nombreUsuario}\\n\\n"`;
                content += `}`;
                content = JSON.parse(content);
                this.infoModal.content = content;
                this.$root.$emit("bv::show::modal", this.infoModal.id, button);
        },

        resetInfoModal() {
            this.infoModal.title = "";
            this.infoModal.content = "";
        },

        async PagarNotaRemision(item) {
            this.disabledButtons = false;
            await this.PreguntarTipoPago();
            await this.ProcederPago(item);
        },

        async PreguntarTipoPago() {
            this.procederPago = 0;
            this.importeAbonar = 0.0;
            await Swal.fire({
                title: 'PAGAR NOTA REMISIÓN',
                text: "¿Que forma de pago deseas?",
                icon: 'question',
                showCancelButton: true,
                confirmButtonColor: "#008000",
				cancelButtonColor: "#3085d6",
                confirmButtonText: 'Totalidad',
                cancelButtonText: 'Abono',
                reverseButtons: false
            }).then((result) => {
                if (result.isConfirmed) {
                    this.pagoTotalNotaRemision = true;
                    this.procederPago = 1;
                } else if (result.dismiss === Swal.DismissReason.cancel) {
                    this.pagoTotalNotaRemision = false;
                    this.procederPago = 1;
                }
            })
        },

        ProcederPago(item) {
            if(this.procederPago) {
                this.notaRemisionSeleccionada = item;
                console.log(this.notaRemisionSeleccionada);
                if(this.pagoTotalNotaRemision) {
                    this.importeAbonar = this.notaRemisionSeleccionada.diferencia;
                }
                this.textoModalPagos = "Abono a Nota de Remisión #"+ item.folio;
                this.modalPago = true;
            }
        },

        async GuardarPago() {
            this.procederPago = true;
            await this.ValidarPago();
        },

        async ValidarPago() {
            console.log('IMPORTE A ABONAR: ', this.importeAbonar);
            if(this.procederPago) {
                if(this.importeAbonar > this.notaRemisionSeleccionada.diferencia) {
                    this.procederPago = false;
                    await Swal.fire({
                        title: "¡ALERTA!",
                        text: "El importe a abonar es mayor que el importe necesario para realizar el pago completo.",
                        icon: "warning",
                    });
                    this.importeAbonar = 0.0;

                } else if (this.importeAbonar <= 0) {
                    this.procederPago = false;
                    await Swal.fire({
                        title: "¡ALERTA!",
                        text: "El importe a abonar debe de ser mayor a 0.",
                        icon: "warning",
                    });
                    this.importeAbonar = 0.0;
                } else {
                    this.disabledButtons = true;
                    await servicio.guardarPago(this.notaRemisionSeleccionada.idNotasRemisionEnc, this.importeAbonar)
                            .then((r) => {
                                if(r.value) {
                                    Swal.fire({
                                        title: "EXITO!",
                                        text: r.message,
                                        icon: "success",
                                    });
                                    this.RestablecerDatos();
                                } else {
                                    this.disabledButtons = true;
                                    Swal.fire({
                                        title: "¡ALERTA!",
                                        text: r.message,
                                        icon: "warning",
                                    });
                                }
                            })
                            .catch((error) => {
                                console.log(error);
                            });
                }
            }
        },

        async RestablecerDatos() {
            await this.ObtenerNotasRemision();
            this.modalPago = false;
            this.disabledButtons = false;
            this.importeAbonar = 0.0;
        }

    }
}
</script>

<style scoped>

    

</style>






